import simpleAddress from '@/components/simple-address/simple-address.nvue';
export default{
	components:{
		simpleAddress
	},
	data(){
		return {
			cityPickerValueDefault: [0, 0, 1],
			address:{
				addressCode:null,
				addressName:null,
			},
			streetName:''
		}
	},
	onLoad() {
		
	},
	methods:{
		//点击弹窗选择地址
		addressChange(){
			this.$refs.simpleAddress.open();
		},
		//确认选择地址
		addressConfirm(e){
			// this.cityPickerValueDefault = e.value 保留当前选择
			this.address = {
				addressCode:e.areaCode,
				addressName:e.label
			}
		},
		// 确定
		submit(){
			this.judgeFun().then(res => {
				let params = {
					addressName:this.address.addressName,
					streetName:this.streetName
				}
				console.log(params,"parmas")
				uni.$emit('houseAddress',params)
				uni.navigateBack()
			}).catch(err => {})
		},
		judgeFun(){
			return new Promise((resolve,reject)=>{
				let tip = ''
				if(!this.address.addressName){
					tip = '请选择地址'
					reject()
				}else if(!this.streetName){
					tip = '请输入户籍办理地点'
					reject()
				}else {
					resolve()
				}
				if(tip){
					uni.showToast({
						icon:'none',
						title:tip
					})
				}
			})
		}
	}
}
